import React, { useState, useCallback, useEffect } from 'react';
import { Box, Container, makeStyles } from '@material-ui/core';
import Page from '../../../components/Page';
import useRank from '../../../hooks/useRank';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import ConsumeChopRuleEditForm from './ConsumeChopRuleEditForm';
import Header from './Header';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import consumeChopRuleService from '../../../services/consumeChopRuleService';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

function ConsumeChopRuleEditView() {
  const { id } = useParams();
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [consumeChopRule, setConsumeChopRule] = React.useState();
  const { ranks } = useRank();
  const { t } = useTranslation();

  const getConsumeChopRule = useCallback(async () => {
    const data = await consumeChopRuleService.get(id);
    setConsumeChopRule(data.data.data);
  }, [isMountedRef]);

  useEffect(() => {
    getConsumeChopRule();
  }, [getConsumeChopRule]);

  if (!consumeChopRule) {
    return null;
  }

  if (!ranks.length) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('Edit Consume Chop Rules')}>
      <Container maxWidth="lg">
        <Header />
        <Box mt={3}>
          <ConsumeChopRuleEditForm consumeChopRule={consumeChopRule} ranks={ranks} />
        </Box>
      </Container>
    </Page>
  );
}

export default ConsumeChopRuleEditView;
